body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-image: url(/static/media/maxresdefault_clouds.0756bae2.jpg);
  background-size: cover;
}

.App {
  text-align: center;
}

.Head{
  margin-bottom: -140px;
  font-size: 50px;
  color: orange;
  letter-spacing: 50px;
  font-family: -webkit-body;
  
}

.LoginCont{
  /* border:1px solid black; */
  display: -webkit-flex;
  display: flex;
  height: -webkit-fill-available;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Login{
  /* border:1px solid black; */
  position: relative;
  top: -60px;
  height: 400px;
  width:400px;
  background: rgba(100, 149, 237, 0.6);
  border-radius: 31px;
  box-shadow: 0px 0px 20px 20px white;
}
.Register{
  height: 550px;
  top:-30px;
}
.loghead{
  font-weight: bolder;
  color: white;
  font-size: 25px;
  outline: 3px ridge white;
  outline-style: dashed;
  outline-color: white;
  outline-width: 6px;
  outline-offset: 9px;
  font-family: -webkit-pictograph;
  text-shadow: 0 0 20px white;
}
.loginfo{
  color: white;
  text-shadow: 0px 0px 20px whitesmoke;
  /* box-shadow: 0 0 20px 9px darkslategrey; */
  font-family: -webkit-body;
  font-size: 20px;
  font-style: italic;
  font-weight: 900;
  background-image: -webkit-linear-gradient(left, darkorange 85%, white);
  background-image: linear-gradient(90deg, darkorange 85%, white);
  border-radius: 15px;
  padding: 4.9px;
}
.logInput{
  border-radius: 15px;
  outline: none;
  padding-left: 5px;
  background: white;
  border: none;
  width:200px;
}

.btn-sign-in{
  padding: 15px;
  width: 250px;
  border: 3px ridge lightsteelblue;
  font-size: 25px;
  font-family: -webkit-body;
  font-weight: bolder;
  border-radius: 40px;
  letter-spacing: 8px;
  box-shadow: 0 0 20px 3px;
}

.darkorange{
  color:darkorange;
  font-weight: bolder;
  -webkit-text-stroke: 0.5px white;
  font-size: 25px;
}

.logo{
  letter-spacing: 5px;
}

.noAccount{
  margin-top: 8px;
  font-weight: 700;
  cursor: pointer;
}

.noAccount:hover{
  color:white;
}
